import React, { Component } from 'react';
import { DatePicker } from 'antd';
import { Select } from 'antd';
import './Calender.scss'
import { mmddformat, compareday, yyyymmfirstweeksate } from '../../Function/Timeformat'
import Calendercell from './Calendercell'
import { Icon } from 'antd'
import Crossection_schedule from '../CrossSectionView/Crosssectionschedule'
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment';

const SCHEDULE_TYPE_EVENT = "äºä»¶";
const SCHEDULE_TYPE_SCHEDULE = "æç¨";
const timeZoneOffset = 8 * 60 * 60 * 1000;

class Calender extends Component {
    state = {
        calendermode: 'week',
        Clickdays: null,
        ClickDate: null,
        displayinfo: null,
        scheduleRawData: [],
        scheduleData: [],
        startTimestamp: 0,
        endTimestamp: 0,
        todayBegin: 0,
        noticeRawData: []
    }
    componentDidMount() {
        const {startTimestamp, endTimestamp, todayBegin} = this.getStartEndTime();
        this.setState({
            startTimestamp: startTimestamp,
            endTimestamp: endTimestamp,
            todayBegin: todayBegin,
            ClickDate: todayBegin
        });
        this.querySchedule(new Date(startTimestamp).toISOString().split('.')[0], new Date(endTimestamp).toISOString().split('.')[0]);
        this.queryNotice();
    }
    getStartEndTime() {
        const today = new Date();
        const monthfirstweekday = new Date(today.getFullYear(), today.getMonth(), 1).getDay();
        const firstCellBegin = new Date(today.getFullYear(), today.getMonth(), 1).getTime() - (monthfirstweekday * 24 * 60 * 60 * 1000) + timeZoneOffset;
        const todayBegin = new Date(today.getFullYear(), today.getMonth(), today.getDate()).getTime() + timeZoneOffset;

        const startTimestamp = (this.state.calendermode === 'week') ? todayBegin -  3 * 24 * 60 * 60 * 1000 : firstCellBegin;
        const endTimestamp   = (this.state.calendermode === 'week') ? startTimestamp + 7 * 24 * 60 * 60 * 1000 : startTimestamp + 35 * 24 * 60 * 60 * 1000;

        return {startTimestamp, endTimestamp, todayBegin};
    }
    querySchedule(startTimestampStr, endTimestampStr) {
        ApiProvider.getOnePatientSchedule(this.props.caseinfo.TRANSINID, startTimestampStr, endTimestampStr)
        .then(data => {
            console.log("getOnePatientSchedule OK " + JSON.stringify(data.data));

            const itemList = data.data;
            let refactoredList = [];
            itemList.forEach(item => {
                const itemTimeStr = item.time.split('T')[0];
                let foundItemIndex = refactoredList.findIndex(element => element.time === itemTimeStr);
                if(foundItemIndex === -1) {
                    //not found, create one
                    let newItem = {
                        time: itemTimeStr,
                        timestamp: item.time,
                        scheduleList: []
                    }
                    let newEvent = {
                        type: item.type,
                        timestamp: new Date(item.time).getTime(),
                        writer: (item.type === SCHEDULE_TYPE_EVENT) ? "æé«å¸«" : "å¼µé«å¸«",
                        name: item.name,//this.props.caseinfo.PBASINGNINFO.PNAMEC,//(item.type === SCHEDULE_TYPE_EVENT) ? "æï¼¯ï¼¯" : "é»ï¼¯ï¼¯",
                        content: item.context,
                        bednumber: item.bednumber
                    }
                    newItem.scheduleList.push(newEvent);
                    refactoredList.push(newItem);
                } else {
                    //found, update item
                    let newEvent = { 
                        type: item.type,
                        timestamp: new Date(item.time).getTime(),
                        writer: (item.type === SCHEDULE_TYPE_EVENT) ? "æé«å¸«" : "å¼µé«å¸«",
                        name: item.name,//this.props.caseinfo.PBASINGNINFO.PNAMEC,//(item.type === SCHEDULE_TYPE_EVENT) ? "æï¼¯ï¼¯" : "é»ï¼¯ï¼¯",
                        content: item.context,
                        bednumber: item.bednumber
                    }
                    refactoredList[foundItemIndex].scheduleList.push(newEvent);
                }
            });
            this.setState({
                scheduleRawData: data.data,
                scheduleData: refactoredList
            });
        })
        .catch(err => {
            console.log("getOnePatientSchedule NG " + JSON.stringify(err));
        });
    }
    queryNotice() {
        ApiProvider.getOnePatientNotice(this.props.caseinfo.TRANSINID, this.props.caseinfo.CASEID)
        .then(result => {
            console.log("getOnePatientNotice OK " + JSON.stringify(result));
            this.setState({
                noticeRawData: result.data
            })
        })
        .catch(err => {
            console.log("getOnePatientNotice NG " + JSON.stringify(err));
        });
    }
    callbackFunction = (timestamp, clickindex) => {
        console.log(timestamp)
        this.setState({
            ClickDate: timestamp,
            displayinfo: clickindex
        })
    }
    onCancelClick() {
        this.setState({
            ClickDate: this.state.todayBegin,
            displayinfo: null
        })
    }
    handleChange = (value) => {
        console.log(value)
        this.setState({
            calendermode: value,
            displayinfo: null,
            Clickdays: null
        })
    }
    onChange = (timestamp) => {
        console.log('changestate')
    }
    onClickCheck(timestampdate) {
        this.setState({
            onClickdays: timestampdate
        })
    }
    getDataFromTimestamp(timestamp) {
        const dateStr = new Date(timestamp).toISOString().split('T')[0];
        const dataOfDayIndex = this.state.scheduleData.findIndex(element => element.time === dateStr);
        const dataOfDay = (dataOfDayIndex === -1) ? {scheduleList: []} : this.state.scheduleData[dataOfDayIndex];
        return dataOfDay;
    }
    getNoticeFromTimestamp(timestamp) {
        const dateStr = new Date(timestamp).toISOString().split('T')[0];
        const noticeOfDayArray = this.state.noticeRawData.filter(element => element.time.indexOf(dateStr) > -1);
        return noticeOfDayArray;
    }
    weekmode() {
        var weekcontext = []
        for (let index = 0; index < 7; index++) {
            const timestampdate = this.state.startTimestamp + (index * 24 * 60 * 60 * 1000);
            const date = mmddformat(timestampdate)
            const dataOfDay = this.getDataFromTimestamp(timestampdate);
            if (index - 3 === 0) {
                weekcontext.push(
                    <Calendercell clickindex={'week'} clickdate={this.state.ClickDate} timestamp={timestampdate} date={date} state={true} newDate={true} data={dataOfDay} today={this.state.todayBegin} parentCallback={this.callbackFunction}></Calendercell>
                );
            }
            else {
                weekcontext.push(
                    <Calendercell clickindex={'week'} clickdate={this.state.ClickDate} timestamp={timestampdate} date={date} state={compareday(timestampdate)} newDate={false} data={dataOfDay} today={this.state.todayBegin} parentCallback={this.callbackFunction}></Calendercell>
                );

            }
        }
        return weekcontext
    }
    weekmonthmode(weeknumber, firstMonthWeekDay) {
        var weekcontext = []
        for (let index = 0; index < 7; index++) {
            const timestampdate = new Date(firstMonthWeekDay).getTime() + (index * 24 * 60 * 60 * 1000) + timeZoneOffset;
            const date = mmddformat(timestampdate)
            const dataOfDay = this.getDataFromTimestamp(timestampdate)

            if (new Date(mmddformat(new Date().getTime())).getTime() <= new Date(mmddformat(new Date(timestampdate).getTime())).getTime()) {
                weekcontext.push(
                    <Calendercell clickindex={weeknumber} clickdate={this.state.ClickDate} timestamp={timestampdate} date={date} state={true} newDate={true} data={dataOfDay} today={this.state.todayBegin} parentCallback={this.callbackFunction}></Calendercell>
                );
            }
            else {
                weekcontext.push(
                    <Calendercell clickindex={weeknumber} clickdate={this.state.ClickDate} timestamp={timestampdate} date={date} state={false} newDate={true} data={dataOfDay} today={this.state.todayBegin} parentCallback={this.callbackFunction}></Calendercell>
                );
            }
        }
        return weekcontext
    }

    ShowModule(rowIndex) {
        if(rowIndex === undefined) rowIndex = 0;
        console.log("this.state.ClickDate" + this.state.ClickDate);

        const dataOfDay = this.getDataFromTimestamp(this.state.ClickDate);
        const noticeOfDay = this.getNoticeFromTimestamp(this.state.ClickDate);

        function checkmode(state) {
            if (state === 'week') {
                return 'calenderdisplayinformation'
            }
            if (state === rowIndex) {
                return 'calenderdisplayinformation'
            }
            return 'calenderdisplayinformationnone'
        }
        console.log(moment(this.state.ClickDate).format("YYYYMMDDHHmmss"))
        return (
            <div className={checkmode(this.state.displayinfo)}>
                <div className='calenderModule'>
                    <t1>è©³ç´°è³è¨</t1>
                    <Icon type="close" onClick={() => this.onCancelClick()} />
                </div>
                <div className='Modulelayout'>
                    <Crossection_schedule PickerDate={this.state.ClickDate} source={1} caseinfo={this.props.caseinfo} noticeData={noticeOfDay} scheduleData={dataOfDay} editstatus={true}></Crossection_schedule>
                </div>
            </div>
        );
    }


    setgroup() {
        var calendermonthgroup = []
        const monthfirstweekdate = new Date(yyyymmfirstweeksate(new Date())).getDay()
        const firstMonthWeekDay = new Date(yyyymmfirstweeksate(new Date())).getTime() - (monthfirstweekdate * 24 * 60 * 60 * 1000)

        for (let index = 0; index < 5; index++) {
            calendermonthgroup.push(
                <div className='calendercontextMonthRows'>
                    {this.weekmonthmode(index, firstMonthWeekDay + (index * 7 * 24 * 60 * 60 * 1000))}
                    {this.ShowModule(index)}
                </div>
            );

        }
        return calendermonthgroup
    }
    modechange(state) {
        if (state === 'week') {
            return (
                <div className='calendercontext'>
                    {this.weekmode()}
                    {this.ShowModule()}
                </div>
            );
        }
        else {
            // return (
            //     <div>
            //         {this.setgroup()}
            //     </div>
            // );
            return this.setgroup()
        }
    }

    SelectDatepickertype() {
        const { MonthPicker } = DatePicker;
        if (this.state.calendermode === 'month') {
            return (
                <MonthPicker placeholder="è«é¸ææä»½" />
            );
        }
        else {
            return (
                <DatePicker placeholder={['è«é¸ææé']} />
            );
        }
    }
    render() {
        const { Option } = Select;
        // const localday = new Date().getTime()
        // function compareday(days) {
        //     if (new Date(days).getTime() > new Date().getTime()) {
        //         return true
        //     }
        //     return false
        // }
        return (
            <div>
                <t1 className='calendertitle'>è¡äºæ</t1>
                <div className='calendercontrol'>
                    {this.SelectDatepickertype()}
                    <Select defaultValue={this.state.calendermode} onChange={this.handleChange}>
                        <Option value="week">é±</Option>
                        <Option value="month">æ</Option>
                    </Select>
                </div>
                {this.modechange(this.state.calendermode)}
            </div>
        );
    }
}

export default Calender