export function statecolor(state) {
    if (state === 'HR') {
        return "#779F50"
    }
    if (state === 'BP') {
        return "#C12528"
    }

    if (state === 'ABP') {
        return "#C12528"
    }
    if (state === 'NBP') {
        return "#C12528"
    }
    if (state === 'SpO2') {
        return "#4773B8"
    }
    if (state === 'BT') {
        return "#7F3DB5"
    }
    if (state === 'RR') {
        return "#DE7531"
    }
    return null
}

