export function NonLabExamReportParser() {
    return [
        {
            parser_name: "CBC",
            apply_condition: {
                name: "CBC|WBC|HB|PLT",
                sample: null,
                lab: null,
                start_time: null,
                end_time: null,
            },
            mappings: [
                {
                    key: "Hb",
                    regex_str: "HB:\\s+(\\d+(.\\d)?)",
                    regex_flags: 'ism',
                    position: 1
                },
                {
                    key: "WBC",
                    regex_str: "wbc:\\s+(\\d+)",
                    regex_flags: 'ism',
                    position: 1
                },
                {
                    key: "PLT",
                    regex_str: "plt:\\s+(\\d+)",
                    regex_flags: 'ism',
                    position: 1
                }
            ],
            summary: "[WBC;-]/[Hb;]/[PLT;-]"
        },
        {
            parser_name: "Atypical Respiratory Panel",
            apply_condition: {
                name: "Atypical Respiratory Panel|CBC|PH|HB|PO2",
                sample: null,
                lab: null,
                start_time: null,
                end_time: null,
            },
            mappings: [
                {
                    key: "Hb",
                    regex_str: "Hb:\\s+\\((\\d+.\\d+)\\)",
                    regex_flags: 'ism',
                    position: 1
                },
                {
                    key: "PH",
                    regex_str: "PH:\\s+\\((\\d+.\\d+)\\)",
                    regex_flags: 'ism',
                    position: 1
                },
                {
                    key: "PO2",
                    regex_str: "PO2:\\s+\\((\\d+)\\)",
                    regex_flags: 'ism',
                    position: 1
                },
                {
                    key: "FIO2",
                    regex_str: "FIO2:\\s+\\((\\d+.\\d+)\\)",
                    regex_flags: 'ism',
                    position: 1
                },
                {
                    key: "HCO3",
                    regex_str: "HCO3:\\s+\\((\\d+.\\d+)\\)",
                    regex_flags: 'ism',
                    position: 1
                }
            ],
            summary: "[PH;-]/[Hb;-]/[PO2;-]/[FIO2;-]/[HCO3;-]"
        }
    ]
}

